<li class:done={task.isDone}>
  <div class="hoverable-area">
    <label class:disabled={disabled}>
      <input type=checkbox bind:checked={task.isDone} on:change={onCheckboxClicked} {disabled}>
      {@html titleWithClickableLinks}
    </label>
    {#if canEdit}
      <a href="#" on:click|preventDefault={removeTask}>&times;</a>
    {/if}
  </div>
</li>

<script>
  import Autolinker from 'autolinker';

  export let task;
  export let onCheckboxClicked;
  export let removeTask;
  export let disabled;
  export let canEdit;

  $: titleWithClickableLinks = Autolinker.link(task.title);
</script>

<style>
  label {
    display: inline-block;
  }

  .disabled {
    cursor: not-allowed;
  }

  a {
    padding: 0px 4px;
    display: none;
  }

  .hoverable-area {
    display: inline-block;
  }

  .hoverable-area:hover a {
    display: inline-block;
  }

  li.done label {
    text-decoration: line-through;
    opacity: .5;
  }

  .hoverable-area > a {
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    font-weight: 900;
  }

  .hoverable-area > a:hover {
    text-decoration: none;
  }

  li {
    margin-bottom: 2px;
  }
</style>
